<template>
    <el-col>
        <el-row>
            <el-table
        :data="tableData.request"
        :key="tableData.name"
      >
                <template v-for="column in tableConfig.request.columns">
                    <el-table-column
                          :key="column.label"
                          :prop="column.prop"
                          :label="column.label"
                          :width="column.width"
                >
                        <template slot-scope="scope">
                            <el-input v-if="column.prop=='value'" v-model="scope.row[column.prop]"></el-input>
                            <div v-if="column.prop!='value'">  {{ scope.row[column.prop]}} </div>
                        </template>
                    </el-table-column>
                </template>
            </el-table>
         <el-divider></el-divider>

        <el-button type="primary" plain  v-on:click="convertRequestMode()" style="float: right;">generate request parameters</el-button>
        </el-row>
        <el-divider></el-divider>
        <el-row>
            <el-table :data="tableData.response" style="width: 100%">
                <el-table-column
          v-for="column in tableConfig.response.columns"
          :key="column.label"
          :prop="column.prop"
          :label="column.label"
          :width="column.width"
        ></el-table-column>
            </el-table>
        </el-row>
    </el-col>
</template>
<script>
import axios from "axios";
export default {
  name: "Simpleform",
  components: {},
  data() {
    return {
      tableConfig: {
        request: {},
        response: {}
      },
      tableData: {}
    };
  },
  created: function() {
    const url = "./config/requests/kline.json";
    this.fetchData(url);
    console.log("created");
  },
  computed: {
    key() {
      return this.$route.name !== undefined
        ? this.$route.name + new Date()
        : this.$route + new Date();
  },
    },
  watch: {
    $route(to, from) {
      const query = to.query;
      const url = `./config/requests/${query["name"]}.json`;
      console.log(from);
      this.fetchData(url);
      this.$root.$emit("access-token", query);

    },
  },
  methods: {
    fetchData(url) {
      this.getRequestTemplate(url)
        .then((data) => {
          this.tableConfig = data["data"]["tableConfig"];
          this.tableData = data["data"]["tableData"];
          this.$root.$emit("table-data", this.tableData);
        })
        .catch((exception) => {
          console.log(exception);
        });
    },
    onSubmit() {
      console.log("submit!");
    },
    async getRequestTemplate(url) {
      return await axios.get(url);
    },
    convertRequestMode() {
      var temp =this.tableData.requestModel;
      console.log(JSON.stringify(this.tableData.request));
      for ( var i = 0; i < this.tableData.request.length; i++) {
      console.log(this.tableData.request[i].parameter+'-----------------'+this.tableData.request[i].value);
      temp[this.tableData.request[i].parameter]=this.tableData.request[i].value;
       }
      console.log(JSON.stringify(temp));
      this.$root.$emit("table-data", this.tableData);
      return this.tableData;
    }
  }
};

</script>
<style scoped></style>



