<template>
    <el-form ref="request-form" :model="form" label-width="80px" label-position="top">
        <el-form-item label="Request URL" style="text-align: left;">
            <el-input v-model="form.url" type="textfield" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="Request JSON" style="text-align: left;">
            <el-input
        type="textarea" autosize
        v-model="form.payload"
        :disabled="true"
      ></el-input>
       </el-form-item>

              <el-form-item  style="text-align: left;">
        <el-button type="primary" plain  v-on:click="onSubmit()" style="float:left">send request</el-button>

        </el-form-item>
        <el-form-item label="Response JSON" style="text-align: left;">
            <el-input
        type="textarea" autosize
        v-model="form.response"
        :disabled="true"
      ></el-input>
        </el-form-item>
    </el-form>
</template>

<script>

var localRequestConfig = {};

var localTableData = {};

export
default {
    name: "RequestPanel",
    components: {},
    data() {
        return {
            form: {
                url: "",
                payload: "",
                response: ""
            },
        };
    },
    mounted() {
        const vm = this;
        this.$root.$on("access-token", function (data) {
            console.log(data);
        });
        this.$root.$on("table-data", function (data) {
            localTableData=data;
            vm.form.url = data.path;
            vm.form.payload = this.$qs.stringify(data.requestModel);
            localRequestConfig.method = data.httpRequestMethod;
            localRequestConfig.url = data.path;
            var name = 'requestModel';
            if (data.httpRequestMethod == 'get')
               {
                localRequestConfig.params = data[name];
                localRequestConfig.headers = {'Content-Type': 'application/x-www-form-urlencoded'};
                // localRequestConfig.paramsSerializer=function (params) {
                //                                          console.log(vm.$qs.stringify(params));
                //                                         return vm.$qs.stringify(params);
                //                                       }
               }
            if (data.httpRequestMethod == 'post')
                localRequestConfig.data = data.requestModel;
            vm.form.response = "";
        });
    },
    methods: {
        onSubmit() {
             if (localTableData.signatureRequired == true)
                {
                 this.generateSignature();
                }
            console.log(localRequestConfig);
            this.$axios.request(localRequestConfig).then(response => (
            this.form.response = JSON.stringify(response.data,null,2),
            console.log(response)

            )).
            catch(function (error) { // 请求失败处理
                console.log(error);
            });
        },
        generateSignature(){




        }
    }
};

</script>


